module App.View

open Elmish
open Elmish.Browser.Navigation
open Elmish.Browser.UrlParser
open Fable.Core
open Fable.Core.JsInterop
open Fable.Import
open Fable.Import.Browser

open Fable.Helpers.React
open Fable.Helpers.React.Props


open Fable.PowerPack
open Fable.PowerPack.Fetch.Fetch_types

open DNN
let containerId = "cfp-spike-container"

//Types
type Model ={
  ModuleId : string
  Ergebnis : string option
}
type Msgs = 
  | HelloWorldFetched of string
  | HelloWorldFailed of exn

//Api
let helloworld moduleId = 
  (*   if moduleId = "DEBUG"
    then
      promise {
        
        return "DEBUG"}
    else *)
      promise {    
          let sf = ServiceFramework moduleId    
          let url = sf.getServiceRoot("fsharp/spike") + "service/" + "helloworld"
          let props = 
              [ RequestProperties.Method HttpMethod.GET
                Fetch.requestHeaders (moduleHeaders sf)
              ]
          return! Fable.PowerPack.Fetch.fetchAs<string> url props
    }

let helloworldCmd moduleId = Cmd.ofPromise helloworld moduleId HelloWorldFetched HelloWorldFailed 

// State
let init model =
  let model = {
    ModuleId =  (document.getElementById containerId).dataset.["moduleid"]
    Ergebnis = None
  }
  model , helloworldCmd model.ModuleId

let update  msg model=
  match msg with
  | HelloWorldFetched s -> {model with Ergebnis = Some s}, Cmd.none
  | HelloWorldFailed  e -> {model with Ergebnis = Some e.Message}, Cmd.none

//view
let root model dispatch =
 div [ ] [str<|sprintf "module-id %A" model]

open Elmish.React
open Elmish.Debug

// App
Program.mkProgram init update root
|> Program.withReact containerId
|> Program.withConsoleTrace
(* //-:cnd
#if DEBUG
|> Program.withDebugger
#endif
//+:cnd *)
|> Program.run


